:root {
  --white: #fff;
  --darkgrey: #56534c;
  --darkred: #a47d5e;
  --grey:#56534c; 
  --bigred: #dfc691;
  --lightgrey: #979285;
  --aleg: 'Alegreya', serif;
  --cedar: 'Cedarville Cursive', cursive;
  --metro: 'Metrophobic', sans-serif;
  --play: 'Playfair Display', serif;
  --sectionTitle: 28px;
  --subTitle: 12px;
  --paraText: 12px;

}

*{
  padding:0;
  margin:0;
  font-family: var(--aleg);
 
}
body{
  background-color:#101010;
}

h3{
  text-transform: capitalize;  
  color: var(--darkgrey);
  font-size: var(--sectionTitle);
}
h3.slick-slide-title{
  padding-bottom: 10px;
}

.Wrapper{
  width:100%;
  max-width: 1200px;
  margin:auto;
}
/*
font-family: 'Alegreya', serif;
font-family: 'Cedarville Cursive', cursive;
font-family: 'Metrophobic', sans-serif;
font-family: 'Playfair Display', serif;
*/

.fixed-menu{
  background-color: rgba(0,0,0,0.5);
  font-family: var(--aleg);
  display:flex;
  justify-content: space-around;
  align-items:center;
  position:fixed;
  margin:0;
  width:100%;
  max-width: 1200px;
}

.logo{
  flex:1;
  padding-top: 5px;
  text-align: center;
}


.main-menu{
  flex:3;
  font-size:14px;
  text-align: center;
}

.main-menu ul  {
  display:flex;
  flex-direction: row;
  justify-content:space-around;
  padding:0;
  margin:0;
  list-style: none;

}

.main-menu ul, .main-menu ul li{
  padding:0; 
  margin:0;
  list-style: none;
  
}

.main-menu a, .main-menu li{
  color: #fff;
  text-decoration: none;
}

.main-menu a.nav-active {
  color: rgb(0, 0, 0);
  /*text-shadow: 1px 1px 1 #fff;*/
  background-color: #fff;
  padding:5px;
}

.main-menu a:hover{
  color: rgb(0, 0, 0);
}

.Social-Media{  
  flex:1;
  text-align: right;
  margin-right: 45px
}
.Social-Media a{
  color:#fff;
  padding-right:15px;
  padding-left: 10px;  
}

.Social-Media a:hover{
  color:#000;
}

/*welcome Page */
.welcome-page{
  float:left;
  width: 100%;
}

.welcome-page >  div{
  width: 100%;
}

.banner{
  display:grid;
  grid-template-columns: repeat (4,1fr);
  grid-template-rows:repeat (5, 200px);
  color: #fff;
}

.banner-img{
  grid-column: 1/-1;
  grid-row: 1/10;
}
.banner-img>img{
  width: 100%;
}

.banner-heading{
  grid-column: 1/-1;
  grid-row: 3/ 4;
  text-align: center;
  font-family: var(--play);
  font-size: 70px;
  font-weight:bold;
  
}

.banner-dot-divider{
  grid-column: 1/-1;
  grid-row: 4/ 5;
  text-align: center;
}

.banner-quotes{
  grid-column: 1/-1;
  grid-row: 5/ 6;
  text-align: center;
  font-family: var(--metro);
  font-size: 20px;
  padding-top: 20px;
}

.banner-button{
  grid-column: 1/-1;
  grid-row: 6/7;
  text-align: center;
  font-family: var(--aleg);
  padding-top: 100px;

}

.banner-button > a > button{
  color: #fff;
  padding: 15px 20px;
  background-color: rgba(116, 116, 116, 0.5);
  border: 1px dashed #fff;
  cursor: pointer;
}

.banner-button > a > button:hover{
  color: #000;
  background-color: #fff;
  border: 1px dashed #fff;
}

.art-of{
  width:100%;
  background: #fff;
  margin-top: -10px;
}

.art-of-left, .art-of-rightt{
  width: 50%;
  float:left;
}

.art-of-right img:hover{
  transform: scale(1.5);
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.art-of-left{
  float:left;
  text-align:center;
}
.art-of-left > h5{
  width: 100%;
  text-align: center;
}
.art-of-left >  p{
  width:80%;
  padding: 0%  10%;
  text-align: center;
  font-size: var(--paraText);
}
.slick-slide-label{
  width: 100%;
  text-align: left;
  font-size: var(--paraText);
}


.featured{
  width:100%;
  text-align:  center;
}
.featured > h2{
  color: var(--darkgrey);
  font-size: 18px;
  width: 100%;
  text-align: center;
  padding:25px 0;
}
.featured > div, .featured > div.quotes{
  display:flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.featured > div > p{
  border-top:  1px solid #56534c;
  border-bottom: 1px solid #56534c;
  padding: 10px  10px;
  margin-top: 10px;
}
.featured > div > img{
  width: 150px;
  height:auto;
  border-radius: 90%;
}
.featured > div.quotes > p{
  border: none;
  font-family: var(--cedar);

}


.art-of-right {
  display:grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
}

.main-featured{
  width:100%;
  height: 750px;
  padding-top:  100px;
  background-image: url(./img/BG_featured.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size:auto;
  float:left;
  

}

.slider-wrapper{
  
  width:80%;
  margin: auto;
  margin-top: 100px;
}

.breakfast{
  width:100%;
  background-image: url(./img/BG_Breakfast.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size:auto;
  float:left;
  padding-top: 100px;
  padding-bottom: 30px;
}
.breakfast-left {
  float:left;
  width: 50%;
}

.breakfast-left h3{
  color:#fff;
  text-align: center;
  font-size: 40px;
}

.breakfast-left h5{
  text-align:center;
}


 h5  {
  /*text-shadow: 1px 1px rgb(116, 116, 116);*/
  text-transform:uppercase;
  font-size: var(--subTitle);
  color: var(--darkred);
  padding-bottom: 50px;
}
h5.slide-slide-subtitle{
  text-align:left;
}

.breakfast-left >p  {
  float:left;
  text-align: center;
  padding-left: 50px;
  color: #fff;
  padding-bottom:  30px;
}

.menu-name{
  font-size: 24px;
  font-weight:  bold;
  padding-top: 20px;
}

.menu-price{
  font-size: 30px;
  color: #c8a593;
  font-weight: bold;
}

.thumbnail-slider-wrap {
  margin-top: 15px;
  height: 85px;
}
.thumbnail-slider-wrap .slick-track .slick-slide {
  text-align: center;
  float:left;
  margin-right: 5px;
}
.thumbnail-slider-wrap .slick-track .slick-slide img {
  width: 70%;
  border-radius: 50%;
}
.slideleft > img.slick-slide-image{
  float:left; 
  width:400px;
  height: auto;
}


.slideleft{
  float:left;
  width: 390px;
  overflow: hidden;
  
  border: 10px solid violet;
  
}
.slideleft img{
  width: 100%;
}

.slideright{
  float:left;
  margin-top:-300px;
  margin-left: 400px;
  width: 390px;
  padding-top: 50px;
  height: auto;
  background: pink;
  border: 10px solid green;
}

.thumbnail-slider-wrap{
  width:390px; 
  float : right;
  margin-right:  100px;
  margin-top:  -175px;
}
.slick-list{
  width: 390px;
}

.main-slide-wrapper{
  float: left;
  width:1000px;
  
  border: 10px solid blue;
}

.breakfast-right{
  float:left;
  background: url(./img/breakfast_menu.png) no-repeat;
  width: 500px;
  height: 600px;
  margin-top: -70px;
  padding: 150px 100px 50px 100px;
  text-align:center;
  
}

.breakfast-right p{
  font-size: 12px;
  margin-top: 50px;
}

.snacks{
  float: left;
  width: 100%;
  background-color: #f4f1ea;
  padding-top: 50px;
  padding-left: 50px;
}

.snacks-title{
  text-align: center;
}

.snack-left{
  float:left;
  font-size: 12px;
  width: 400px;
  text-align: center;
  padding: 0 50px;
}

.snack-left > img {
    width: 100px;
    height: 100px;
  border-radius: 50%;
  object-fit:cover;
}


.snack-left .title{
  font-size: 25px;
  color: #c8a593;
  font-weight: bold; 
}

.snack-left p{
  padding-bottom: 30px;
}

.snack-right{
  float:left;
  margin-top: 50px;
  width: 700px;
  height: 500px;
  position:relative;
  
}
.snack-right-wrapper{
  position:absolute;
  width: 700px;
  height: 500px;
  border: 10px solid #fff;
  overflow:hidden;
  top:0;
  z-index: 9;  
}

.snack-right img {
  float:left;
}
.pre-mins{
  position: absolute;
  width: 200px;
  height: 520px;
  overflow: hidden;
  background: #fff;
  opacity: 0.8;
  top: -10px;
  right: 10px;
  z-index: 10;
  text-align: center;
  padding: 20px 0;
}


.pre-mins h3{
  color: #c8a593;
  font-weight: bold;
  text-transform:uppercase;
  font-size: 16px;
}

.pre-mins h2{
  color: var(--darkgrey);
  font-size: 50px;
  
}

.pre-mins p{
  margin-bottom: 50px;
  font-size: 12px;
  color: var(--grey);
}

.ready-in{
  background: url(./img/BG.png) no-repeat;
}

.ready-in h2{
  font-size: 60px;
  color: #c8a593;
}

button.recipe-btn {
  color: #fff;
    padding: 15px 20px;
    background-color: #fff;
    border: 1px dashed #c8a593;
    cursor: pointer;
    color: #c8a593; 
}

button.recipe-btn:hover {
      cursor: pointer;
    color: #000; 
    background-color: #dfc691;
}

.recipe-link{
  position: absolute;
  bottom: -10px;
  left: -10px;
  z-index: 10;
}

.footer{
  float:left;
  width: 100%;
  background-color: #f4f1ea;
  padding: 50px 0 40px 0;
}
.footer-wrapper{
  width: 80%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer-left{
  padding: 20px 30px;
  flex: 2;
  border-top: 1px solid #56534c;
  border-bottom: 1px solid #56534c;
  display:flex;
}
.footer-left a{
 
  text-decoration: none;
  font-family: 'Alegreya', serif;
  color: #56534c;
  flex:1;
  font-size: 12px;
  
}
.footer-left a:hover{
  color: #000;
}
.footer-center{
  padding: 10px 20px;
  flex: 1;
}

.work-on-progress{
  padding: 100px ;
  color: #101010;
  font-size: 50px;
  width: 100%;
  float:left;
  background: #fff;
  text-align: center;
}