* {
    box-sizing: border-box;
    margin: 0;
    font-family: 'Alegreya', serif;
  }
  
  :root {
    --heights: 50vh;
    --widths: 100%;
  }
  
  .slider-container {
    height: 500px;
    width: 800px;
    position: relative;
    margin: auto;
    overflow: hidden;
    margin: 0 auto;
    background-color: #fff;
  }
  
  .active {
    display: inline-block;
    animation: fadeIn 3s;
  -webkit-animation: fadeIn 3s;
  -moz-animation: fadeIn 3s;
  -o-animation: fadeIn 3s;
  -ms-animation: fadeIn 3s; 
  }

  @keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }
  
  @-moz-keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }
  
  @-webkit-keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }
  
  @-o-keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }
  
  @-ms-keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }
  
  .inactive {
    
    animation: fadeOut 5s;
  -webkit-animation: fadeOut 5s;
  -moz-animation: fadeOut 5s;
  -o-animation: fadeOut 5s;
  -ms-animation: fadeOut 5s; 
  display: none;
  }
  @keyframes fadeOut {
    0% { opacity: 1; }
    100% { opacity: 0; }
  }
  
  @-moz-keyframes fadeOut {
    0% { opacity: 1; }
    100% { opacity: 0; }
  }
  
  @-webkit-keyframes fadeOut {
    0% { opacity: 1; }
    100% { opacity: 0; }
  }
  
  @-o-keyframes fadeOut {
    0% { opacity: 1; }
    100% { opacity: 0; }
  }
  
  @-ms-keyframes fadeOut {
    0% { opacity: 1; }
    100% { opacity: 0; }
  }
  
  .slides {
    height: 550px;
    width: 800px;
    position: relative;
    margin:  0 auto;
    transition: opacity 5s ease;
  }
  .slideDescription{
    display: block;
  }
    
  .slide-image {
    width: 400px;
    height: auto;
    position: absolute;
    object-fit: cover;
    
  }
  
  .slide-image-small{
    width: 100px;
    height:auto;
  }
  
  .slide-text { 
    color: #000;
    font-size: 12px!important;
  }

  .image-desc{
    position:absolute;
    z-index:10;
    left: 400px;
    top: 100px;
    text-align:left; 
    width: 400px;
    height: 400px;
  }

  .slide-title{
    
    color: #56534c;
    font-size: 28px;
    font-weight: bold;
    
   }

   .slide-sub{
      width: 400px;
      color: #a47d5e;
      font-size:12px;
      text-transform: uppercase;
      
  }
  
  .slide-text {
    top: 65%;
    font-size: 2rem;
  }
  
  .prev,
  .next {
    color: transparent;
    cursor: pointer;
    z-index: 100;
    position: absolute;
    top: 50%;
    width: auto;
    padding: 1rem;
    margin-top: -3rem;
    font-size: 40px;
    font-weight: bold;
    border-radius: 0px 5px 5px 0px;
    opacity: 50%;
  }
  
  .slider-container:hover .prev,.slider-container:hover .next {
    color: black
  }
  
  .slider-container:hover .prev:hover,
  .slider-container:hover .next:hover {
    color: white;
    background-color: rgba(0, 0, 0, 0.6);
    transition: all 0.5s ease-in;
  }
  
  .next {
    right: 0;
    border-radius: 5px 0px 0px 5px;
  }
  
  .all-dots {
    width: 100%;
    height:auto;
    position: absolute;
    display: flex;
    top: 400px; 
    left: 0px;
    justify-content: center;
    z-index: 200;
  }
  
  .dot {
    cursor: pointer;
    height:100px;
    width: 100px;
    margin: 0px 3px;
    background-color: transparent;
    /* background-color: rgba(0, 0, 0, 0.3); */
    border-radius: 50%;
    display: inline-block;
  }
  .dot img{
    border-radius: 50%;
    opacity: 50%;
  }
  .dot img:hover, .slider-container:hover .active-dot img{
    opacity: 100%;
  }
